<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/admin' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <!--  -->
      <!--  -->
      <el-switch
        v-model="queryInfo.type"
        active-text=""
        inactive-text=""
        @change="switchChange">
        </el-switch>
      <el-table :data="eqList" border stripe>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="rid" label=""></el-table-column>
        <el-table-column prop="eid" label=""></el-table-column>
        <el-table-column prop="ename" label=""></el-table-column>
        <el-table-column prop="aid" label=""></el-table-column>
        <el-table-column prop="uname" label=""></el-table-column>
        <!-- <el-table-column prop="emanufacture" label=""></el-table-column> -->
        <el-table-column prop="date" label="">
            <template slot-scope="scope">{{scope.row.date| datetimeFormat}}</template>
        </el-table-column>
        <el-table-column prop="repairdate" label="" v-if="!queryInfo.type">
          <template slot-scope="scope">{{scope.row.repairdate| datetimeFormat}}</template>
        </el-table-column>
        <el-table-column prop="state" label="" width="60px">
            <template slot-scope="scope">{{scope.row.state === '0' ? '' : '' }}</template>
        </el-table-column>
        <el-table-column label="" width="80px" v-if="queryInfo.type">
          <template slot-scope="scope">
            <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
              <el-button
                type="success"
                icon="el-icon-check"
                size="mini"
                @click="repairedEqById(scope.row.eid)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //
      queryInfo: {
        type: true,
        query: "",
        pagenum: 1,
        pagesize: 5
      },
      eqList: [],
      total: 0,
      setRoleDialogVisible: false,
      userInfo: {},
      rolelist: [],
      selectedRoleId: "",

      aid: null
    };
  },
  created() {
    this.getEqList();
  },
  mounted() {
    this.aid = window.sessionStorage.getItem('aid')
  },
  methods: {
    //
    switchChange(e) {
        // console.log(e)
        // 
        this.getEqList()
    },
    //
    async getEqList() {
      const { data: res } = await this.$http.get("repaireqlist", {
        params: this.queryInfo
      });
      if (res.meta.status === 202) {
        this.eqList = [];
        this.total = 0;
        return;
      }
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg);
      }
      this.tableType = res.data.type
      this.eqList = res.data.eq;
      this.total = res.data.total;
    },
    //pagesize
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize;
      this.getEqList();
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage;
      this.getEqList();
    },
    async repairedEqById(eid) {
        const { data: res } = await this.$http.put("repaired", { eid: eid });
        if (res.meta.status !== 200) {
            return this.$message.error("");
        }
        this.$message.success("");
        this.getEqList();
    }
  }
};
</script>

<style lang="less" scoped>
.el-select {
  width: 80px;
}

</style>
