<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/admin' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row :gutter="20">
        <el-col :span="7">
          <el-input placeholder="" v-model="sel.eid" clearable>
            <el-button slot="append" icon="el-icon-search" @click="geteq"></el-button>
          </el-input>
        </el-col>
        <el-col :span="2">
          <el-button type="primary" @click="lendDialogOpen"></el-button>
        </el-col>
        <el-col :span="2">
          <el-button type="info" @click="deltabbleData"></el-button>
        </el-col>
      </el-row>
      <el-table :data="tableData" border stripe>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="eid" label=""></el-table-column>
        <el-table-column prop="ename" label=""></el-table-column>
        <!-- <el-table-column prop="num" label=""></el-table-column>
        <el-table-column prop="snum" label=""></el-table-column> -->
        <el-table-column prop="emanufacture" label=""></el-table-column>
        <el-table-column prop="buydate" label="">
          <template slot-scope="scope">{{scope.row.buydate| dateFormat}}</template>
        </el-table-column>
        <el-table-column prop="address" label=""></el-table-column>
        <el-table-column prop="state" label=""></el-table-column>
        <el-table-column fixed="right" label="" width="80">
          <template slot-scope="scope">
            <el-button type="text" size="small" @click="deltableDataItem(scope.$index)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="lendDialogVisible" width="30%" @close="lendDialogClosed">
      <el-form :rules="lendFormRules" ref="lendFormRef" label-width="70px" :model="userForm">
        <el-form-item label="" prop="uid">
          <el-input v-model="userForm.uid"></el-input>
        </el-form-item>
        <!--  -->
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="eqLend"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      aid: null,
      sel: {
        eid: ""
      },
      tableData: [],
      nowTable: [],
      userForm: {
        uid: ""
      },
      lendDialogVisible: false,
      lendFormRules: {
        uid: [
          { min: 3, max: 10, message: " 3  10 ", trigger: "blur" },
          { required: true, message: "", trigger: "blur" }
        ]
      },
      path: "ws://127.0.0.1:8005",
      socket: ""
    };
  },
  mounted() {
    this.aid = window.sessionStorage.getItem('aid')
    // 
    // this.init();
  },
  methods: {
    init() {
      if (typeof WebSocket === "undefined") {
        alert("socket");
      } else {
        // socket
        this.socket = new WebSocket(this.path);
        // socket
        this.socket.onopen = this.open;
        // socket
        this.socket.onerror = this.error;
        // socket
        this.socket.onmessage = this.getMessage;
      }
    },
    open() {
      console.log("socket");
      var uid = "uid2";
      this.socket.send("game2");
    },
    error() {
      console.log("");
    },
    getMessage(msg) {
      this.sel = {
        eid: msg.data
      }
      this.geteq()
    },
    send() {
      this.socket.send(params);
    },
    close() {
      console.log("socket");
    },
    //
    async geteq() {
      if (this.sel.eid === "") {
        return this.$message.error("");
      }
      var that = this;
      //eidnowTable
      var iseid = false;
      console.log()
      this.nowTable.find(function(value) {
        if (value === that.sel.eid) {
          iseid = true;
        }
      });
      if (iseid) {
        return this.$message.error("");
      }
      const { data: res } = await this.$http.get("lendseleq", { params: this.sel });
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg);
      }
      if (res.data[0].state !== '') {
        return this.$message.error('');
      }
      this.nowTable.push(res.data[0].eid);
      this.tableData.push(res.data[0]);
    },
    // 
    deltableDataItem(index) {
      this.nowTable.splice(index, 1);
      this.tableData.splice(index, 1);
    },
    // 
    deltabbleData() {
      this.tableData = [];
      this.nowTable = [];
    },
    //
    async eqLend() {
      // valid=true
      this.$refs.lendFormRef.validate(async valid => {
        if (!valid) return;
        console.log('dianji')
        const { data: res } = await this.$http.post("eqlend", {
          eqlend: this.nowTable,
          uid: this.userForm.uid,
          aid: this.aid
        });
        console.log(res.meta.status)
        if (res.meta.status !== 200) {
          return this.$message.error(res.meta.msg);
        }
        this.$message.success("");
        this.lendDialogVisible = false;
        this.deltabbleData();
      });
    },
    lendDialogOpen() {
      if (this.nowTable.length === 0) {
        return this.$message.error("");
      }
      this.lendDialogVisible = true;
    },
    lendDialogClosed() {
      this.$refs.lendFormRef.resetFields();
    }
  },
  destroyed() {
    // 
    // this.socket.onclose = this.close;
  }
};
</script>

<style>
</style>
