<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/admin' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row :gutter="20">
        <el-col :span="7">
          <el-input placeholder="" v-model="sel.eid" clearable>
            <el-button slot="append" icon="el-icon-search" @click="geteq"></el-button>
          </el-input>
        </el-col>
        <el-col :span="2">
          <el-button type="primary" @click="eqBack"></el-button>
        </el-col>
        <el-col :span="2">
          <el-button type="info" @click="deltabbleData"></el-button>
        </el-col>
      </el-row>
      <el-table :data="tableData" border stripe>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="eid" label=""></el-table-column>
        <el-table-column prop="uid" label=""></el-table-column>
        <el-table-column prop="emanufacture" label=""></el-table-column>
        <el-table-column prop="uname" label=""></el-table-column>
        <el-table-column prop="uname" label=""></el-table-column>
        <el-table-column prop="outdate" label="">
          <template slot-scope="scope">{{scope.row.outdate| dateFormat}}</template>
        </el-table-column>
        <el-table-column prop="days" label="">
          <template slot-scope="scope">{{scope.row.days + ''}}</template>
        </el-table-column>
        <el-table-column fixed="right" label="" width="80">
          <template slot-scope="scope">
            <el-button type="text" size="small" @click="deltableDataItem(scope.$index)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      aid: null,
      sel: {
        eid: ""
      },
      tableData: [],
      nowTable: [],
      backFormRules: {
        uid: [
          { min: 3, max: 10, message: " 3  10 ", trigger: "blur" },
          { required: true, message: "", trigger: "blur" }
        ]
      },
      path: "ws://127.0.0.1:8005",
      socket: ""
    };
  },
  mounted() {
    this.aid = window.sessionStorage.getItem('aid')
    // 
    // this.init();
  },
  methods: {
    init() {
      if (typeof WebSocket === "undefined") {
        alert("socket");
      } else {
        // socket
        this.socket = new WebSocket(this.path);
        // socket
        this.socket.onopen = this.open;
        // socket
        this.socket.onerror = this.error;
        // socket
        this.socket.onmessage = this.getMessage;
      }
    },
    open() {
      console.log("socket");
      var uid = "uid2";
      this.socket.send("game2");
    },
    error() {
      console.log("");
    },
    getMessage(msg) {
      this.sel = {
        eid: msg.data
      }
      this.geteq()
    },
    send() {
      this.socket.send(params);
    },
    close() {
      console.log("socket");
    },
    //
    async geteq() {
      if (this.sel.eid === "") {
        return this.$message.error("");
      }
      var that = this;
      //eidnowTable
      var iseid = false;
      this.nowTable.find(function(value) {
        if (value === that.sel.eid) {
          iseid = true;
        }
      });
      if (iseid) {
        return this.$message.error("");
      }
      const { data: res } = await this.$http.get("backseleq", { params: this.sel });
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg);
      }
      if (res.data[0].state !== '') {
        return this.$message.error('');
      }
      //
      const nowdate = new Date()
      const nd = nowdate.getTime()
      const outdate = new Date(res.data[0].outdate)
      const od = outdate.getTime()
      res.data[0].days = parseInt((nd - od) / (1000 * 60 * 60 * 24))
      this.nowTable.push(res.data[0].eid);
      this.tableData.push(res.data[0]);
    },
    // 
    deltableDataItem(index) {
      this.nowTable.splice(index, 1);
      this.tableData.splice(index, 1);
    },
    // 
    deltabbleData() {
      this.tableData = [];
      this.nowTable = [];
    },
    //
    async eqBack() {
      if (this.nowTable.length === 0) {
        return this.$message.error("");
      }
      const { data: res } = await this.$http.post("eqback", {
        eqback: this.nowTable,
        aid: this.aid
      });
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg);
      }
      this.$message.success("");
      this.backDialogVisible = false;
      this.deltabbleData();
    }
  },
  destroyed() {
    // 
    // this.socket.onclose = this.close;
  }
};
</script>

<style lang="less" scoped>
  .el-table .warning-row {
    background: oldlace;
  }

  .el-table .success-row {
    background: #f0f9eb;
  }
</style>
