<template>
  <div class="login_container">
    <div class="login_box">
      <!--  -->
      <div class="avater_box">
        <img src="../assets/logo.png" alt />
      </div>
      <!--  -->
      <el-form
        ref="loginFormRef"
        label-width="0px"
        class="login_form"
        :model="loginForm"
        :rules="loginFormRules"
      >
        <el-form-item prop="uname">
          <el-input prefix-icon="iconfont icon-user" placeholder="" v-model="loginForm.uid"></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input type="password" prefix-icon="iconfont icon-3702mima" placeholder="" v-model="loginForm.pw"></el-input>
        </el-form-item>
        <el-form-item>
          <template>
            <el-radio v-model="loginForm.type" label="user"></el-radio>
            <el-radio v-model="loginForm.type" label="admin"></el-radio>
          </template>
        </el-form-item>
        <!--  -->
        <el-form-item class="btns">
          <el-button type="primary" @click="login"></el-button>
          <el-button type="info" @click="resetloginForm"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //
      loginForm: {
        uid: "",
        pw: "",
        type: "user"
      },
      //    
      loginFormRules: {
        uid: [
          { required: true, message: "ID", trigger: "blur" },
          { min: 3, max: 10, message: " 3  10 ", trigger: "blur" }
        ],
        pw: [
          { required: true, message: "", trigger: "blur" },
          { min: 6, max: 15, message: " 6  15 ", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    //    
    resetloginForm() {
      this.$refs.loginFormRef.resetFields();
    },
    login() {
      //
      this.$refs.loginFormRef.validate(async valid => {
        if (!valid) return;
        const { data: res } = await this.$http.post("login", this.loginForm);
        console.log(res);

        if (res.meta.status !== 200) return this.$message.error(res.meta.msg);
        this.$message.success("");
        //  
        if (this.loginForm.type === 'admin') {
          window.sessionStorage.setItem('utype', this.loginForm.type)
          window.sessionStorage.setItem('aid', this.loginForm.uid)
          this.$router.push("/admin");
        } else {
          window.sessionStorage.setItem('utype', this.loginForm.type)
          window.sessionStorage.setItem('uid', this.loginForm.uid)
          this.$router.push("/user");
        }
      });
    }
  }
};
</script>

<style lang="less" scoped>
.login_container {
  background-color: #2b4b6b;
  height: 100%;
}

.login_box {
  width: 450px;
  height: 300px;
  background-color: #fff;
  border-radius: 3px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.avater_box {
  height: 130px;
  width: 130px;
  border: 1px solid #eeeeee;
  border-radius: 50%;
  padding: 10px;
  box-shadow: 0 0 10px #ddd;
  position: relative;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: #eee;
  }
}

.btns {
  display: flex;
  justify-content: flex-end;
}

.login_form {
  position: absolute;
  bottom: 0;
  width: 100%;
  box-sizing: border-box;
  padding: 0 10px;
}
</style>
