<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/admin' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row :gutter="20">
        <el-col :span="7">
          <el-input :placeholder="queryInfo.type == 0 ? '' : ''" v-model="queryInfo.query" clearable @clear="getEqList" class="input-with-select">
            <el-select v-model="queryInfo.type" slot="prepend" placeholder="">
              <el-option label=" " value="0"></el-option>
              <el-option label=" " value="2"></el-option>
              <el-option label=" " value="1"></el-option>
            </el-select>
            <el-button slot="append" icon="el-icon-search" @click="getEqList()"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <!--  -->
      <el-table :data="eqList" border stripe v-if="tableType != 1">
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="eid" label=""></el-table-column>
        <el-table-column prop="ename" label=""></el-table-column>
        <!-- <el-table-column prop="num" label=""></el-table-column>
        <el-table-column prop="snum" label=""></el-table-column> -->
        <el-table-column prop="emanufacture" label=""></el-table-column>
        <el-table-column prop="buydate" label="">
          <template slot-scope="scope">{{scope.row.buydate| dateFormat}}</template>
        </el-table-column>
        <el-table-column prop="address" label=""></el-table-column>
        <el-table-column prop="state" label=""></el-table-column>
        <el-table-column label="" width="180px">
          <template slot-scope="scope">
            <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="showEditDialog(scope.row)"
              ></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click="removeEqById(scope.row.eid)"
              ></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
              <el-button
                type="warning"
                icon="el-icon-paperclip"
                size="mini"
                @click="repairEqById(scope.row.eid,scope.row.state)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-table :data="eqList" border stripe v-if="tableType == 1">
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="ename" label=""></el-table-column>
        <el-table-column prop="num" label=""></el-table-column>
        <el-table-column prop="emanufacture" label=""></el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>
    <!-- t -->
    <el-dialog title="" :visible.sync="addDialogVisible" width="30%" @close="addDialogClosed">
      <!--  -->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px">
        <el-form-item label="" prop="eid">
          <el-input v-model="addForm.eid"></el-input>
        </el-form-item>
        <el-form-item label="" prop="ename">
          <el-input v-model="addForm.ename"></el-input>
        </el-form-item>
        <el-form-item label="" prop="emanufacture">
          <el-input v-model="addForm.emanufacture"></el-input>
        </el-form-item>

        <el-form-item label="" prop="buydate">
          <!-- <el-input v-model="addForm.buydate" disabled></el-input> -->
          <el-date-picker
            v-model="addForm.buydate"
            type="date"
            placeholder=""
            disabled
            >
          </el-date-picker>
        </el-form-item>
         <el-form-item label="" prop="state">
          <el-input v-model="addForm.state" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="address">
          <el-input v-model="addForm.address"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addEq"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="30%" @close="editDialogClosed">
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px">
        <el-form-item label="" prop="eid">
          <el-input v-model="editForm.eid" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="ename">
          <el-input v-model="editForm.ename"></el-input>
        </el-form-item>
        <el-form-item label="" prop="emanufacture">
          <el-input v-model="editForm.emanufacture"></el-input>
        </el-form-item>
        <el-form-item label="" prop="buydate">
          <!-- <el-input v-model="editForm.buydate" disabled></el-input> -->
          <el-date-picker
            v-model="editForm.buydate"
            type="date"
            placeholder=""
            disabled
            >
          </el-date-picker>
        </el-form-item>
         <el-form-item label="" prop="state">
          <el-select v-model="editForm.state" placeholder="" style="width: 220px" disable>
            <el-option label="" value=""></el-option>
            <!-- <el-option label="" value=""></el-option> -->
            <el-option label="" value=""></el-option>
            <!-- <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option> -->
          </el-select>
        </el-form-item>

        <el-form-item label="" prop="address">
          <el-input v-model="editForm.address"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editEqInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //
      queryInfo: {
        type: '0',
        query: "",
        pagenum: 1,
        pagesize: 5
      },
      eqList: [],
      total: 0,
      addDialogVisible: false,
      addForm: {
        eid: "",
        ename: "",
        address: "",
        state: '',
        emanufacture: "",
        buydate: new Date()
      },
      addFormRules: {
        eid: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 10, message: " 3  10 ", trigger: "blur" }
        ],
        ename: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 10, message: " 3  10 ", trigger: "blur" }
        ],
        emanufacture: [
          { required: true, message: "", trigger: "blur" }
        ],
        address: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      //
      editDialogVisible: false,
      editForm: {},
      editFormRules: {
        ename: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 10, message: " 3  10 ", trigger: "blur" }
        ],
        emanufacture: [
          { required: true, message: "", trigger: "blur" }
        ],
        address: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      setRoleDialogVisible: false,
      userInfo: {},
      rolelist: [],
      selectedRoleId: "",
      tableType: "0",
      aid: null
    };
  },
  created() {
    this.getEqList();
  },
  mounted() {
    this.aid = window.sessionStorage.getItem('aid')
  },
  methods: {
    //
    async getEqList() {
      const { data: res } = await this.$http.get("eqlist", {
        params: this.queryInfo
      });
      if (res.meta.status === 202) {
        this.eqList = [];
        this.total = 0;
        return;
      }
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg);
      }
      console.log(res.data.eq)
      this.tableType = res.data.type
      this.eqList = res.data.eq;
      this.total = res.data.total;
    },
    //pagesize
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize;
      this.getEqList();
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage;
      this.getEqList();
    },
    //
    addDialogClosed() {
      this.$refs.addFormRef.resetFields();
    },
    addEq() {
      // valid=true
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return;
        const { data: res } = await this.$http.post("addeq", this.addForm);
        if (res.meta.status !== 200) {
          return this.$message.error(",");
        }
        this.$message.success("");
        this.addDialogVisible = false;
        this.getEqList();
      });
    },
    //
    showEditDialog(row) {
      this.editForm = row;
      this.editDialogVisible = true;
    },
    editDialogClosed() {
      this.$refs.editFormRef.resetFields();
    },
    //
    editEqInfo() {
      // 
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return;
        const { data: res } = await this.$http.put("editeq", this.editForm);
        if (res.meta.status !== 200) {
          return this.$message.error("");
        }
        this.$message.success("");
        this.editDialogVisible = false;
        this.getEqList();
      });
    },
    //
    async removeEqById(eid) {
      //
      const confirmResult = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      ).catch(err => {
        return err;
      });
      // confirm  cnacel
      if (confirmResult !== "confirm") {
        return this.$message.info("");
      }
      const { data: res } = await this.$http.delete("deleq?eid=" + eid);
      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      this.$message.success("");
      this.getEqList();
    },
    async repairEqById(eid,state) {
      if (state === '' || state === '') {
        return this.$message.error("");
      }
      //
      const confirmResult = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      ).catch(err => {
        return err;
      });
      // confirm  cnacel
      if (confirmResult !== "confirm") {
        return this.$message.info("");
      }
      const { data: res } = await this.$http.post("repaireq", { eid: eid,aid: this.aid });
      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      this.$message.success("");
      this.getEqList();
    }
  }
};
</script>

<style lang="less" scoped>
.el-select {
  width: 80px;
}

</style>
