<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/admin' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row :gutter="20">
        <el-col :span="7">
          <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getUserList">
            <el-button slot="append" icon="el-icon-search" @click="getUserList()"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true"></el-button>
        </el-col>
      </el-row>
      <el-table :data="userList" border stripe>
        <el-table-column type="expand" @click="getUserEq">
          <template slot-scope="scope">{{ scope.row.eid }}</template>
        </el-table-column>
        <el-table-column prop="uid" label=""></el-table-column>
        <el-table-column prop="uname" label=""></el-table-column>
        <el-table-column prop="email" label=""></el-table-column>
        <el-table-column prop="mobile" label=""></el-table-column>
        <el-table-column label="" width="180px">
          <template slot-scope="scope">
            <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="showEditDialog(scope.row)"
              ></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click="removeUserById(scope.row.uid)"
              ></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
              <el-button
                type="warning"
                icon="el-icon-setting"
                size="mini"
                @click="resetpwById(scope.row.uid)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>
    <!-- t -->
    <el-dialog title="" :visible.sync="addDialogVisible" width="30%" @close="addDialogClosed">
      <!--  -->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px">
        <el-form-item label="" prop="uid">
          <el-input v-model="addForm.uid"></el-input>
        </el-form-item>
        <el-form-item label="" prop="uname">
          <el-input v-model="addForm.uname"></el-input>
        </el-form-item>
        <el-form-item label="" prop="pw">
          <el-input v-model="addForm.pw" type="password"></el-input>
        </el-form-item>

        <el-form-item label="" prop="email">
          <el-input v-model="addForm.email"></el-input>
        </el-form-item>

        <el-form-item label="" prop="mobile">
          <el-input v-model="addForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addUser"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="30%" @close="editDialogClosed">
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px">
        <el-form-item label="">
          <el-input v-model="editForm.uname" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="editForm.email"></el-input>
        </el-form-item>

        <el-form-item label="" prop="mobile">
          <el-input v-model="editForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editUerInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    //
    var checkEmail = (rule, value, cb) => {
      const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/;
      if (regEmail.test(value)) {
        return cb();
      }
      cb(new Error(""));
    };
    //
    var checkMobile = (rule, value, cb) => {
      const regMobile = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]}18[57]|14[57])[0-9]{8}$/;
      if (regMobile.test(value)) {
        return cb();
      }
      cb(new Error(""));
    };
    return {
      //
      queryInfo: {
        query: "",
        pagenum: 1,
        pagesize: 5
      },
      userList: [],
      total: 0,
      addDialogVisible: false,
      addForm: {
        uid: "",
        uname: "",
        pw: "",
        email: "",
        mobile: ""
      },
      addFormRules: {
        uid: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 10, message: " 3  10 ", trigger: "blur" }
        ],
        uname: [
          { required: true, message: "", trigger: "blur" },
          { min: 2, max: 10, message: " 2  10 ", trigger: "blur" }
        ],
        pw: [
          { required: true, message: "", trigger: "blur" },
          { min: 6, max: 15, message: " 6  15 ", trigger: "blur" }
        ],
        email: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkEmail, trigger: "blur" }
        ],
        mobile: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkMobile, trigger: "blur" }
        ]
      },
      //
      editDialogVisible: false,
      editForm: {},
      editFormRules: {
        email: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkEmail, trigger: "blur" }
        ],
        mobile: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkMobile, trigger: "blur" }
        ]
      },
      setRoleDialogVisible: false,
      userInfo: {},
      rolelist: [],
      selectedRoleId: ""
    };
  },
  created() {
    this.getUserList();
  },
  methods: {
    //
    async getUserList() {
      const { data: res } = await this.$http.get("userlist", {
        params: this.queryInfo
      });
      if (res.meta.status === 202) {
        this.userList = [];
        this.total = 0;
        return;
      }
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg);
      }
      this.userList = res.data.users;
      this.total = res.data.total;
    },
    //
    getUserEq() {

    },
    //pagesize
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize;
      this.getUserList();
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage;
      this.getUserList();
    },
    //
    addDialogClosed() {
      this.$refs.addFormRef.resetFields();
    },
    addUser() {
      // valid=true
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return;
        const { data: res } = await this.$http.post("adduser", this.addForm);
        if (res.meta.status !== 200) {
          return this.$message.error(",");
        }
        this.$message.success("");
        this.addDialogVisible = false;
        this.getUserList();
      });
    },
    //
    showEditDialog(row) {
      this.editForm = row;
      this.editDialogVisible = true;
    },
    editDialogClosed() {
      this.$refs.editFormRef.resetFields();
    },
    //
    editUerInfo() {
      // 
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return;
        const { data: res } = await this.$http.put("edituser", this.editForm);
        if (res.meta.status !== 200) {
          return this.$message.error("");
        }
        this.$message.success("");
        this.editDialogVisible = false;
        this.getUserList();
      });
    },
    //
    async removeUserById(uid) {
      //
      const confirmResult = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      ).catch(err => {
        return err;
      });
      // confirm  cnacel
      if (confirmResult !== "confirm") {
        return this.$message.info("");
      }
      const { data: res } = await this.$http.delete("deluser?uid=" + uid);
      if (res.meta.status !== 200) {
        this.$message.error("");
      }
      this.$message.success("");
      this.getUserList();
    },
    //
    async resetpwById(uid) {
      //
      const confirmResult = await this.$confirm(
        "" + uid + "123456, ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      ).catch(err => {
        return err;
      });
      // confirm  cnacel
      if (confirmResult !== "confirm") {
        return this.$message.info("");
      }
      const { data: res } = await this.$http.put("resetpw", { uid: uid });
      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      this.$message.success("");
    }
  }
};
</script>

<style lang="less" scoped>
</style>
